.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 5vh;
  padding: 2.5vh  0 2.5vh 0;
  width: 100%;
  color: white;
}

.App-title {
  font-size: calc(14px + 0.75vw);
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.location-list {
  background-color: #222;
  height: 45vh;
  padding: 2.5vh  0 2.5vh 0;
  width: 100%;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.location-list-items {
height: 100%;
width: 50%;
}

.location-list-elements-container {
  height: 75%;
  overflow: auto;
}

.location-list-elements {
  padding: 0;
  margin-bottom: 0;
  list-style-type: none;
  overflow: auto;
  font-size: calc(8px + 0.75vw + 0.5vh);
}

.location-list-info {
  height: 100%;
  width: 50%;
}

.map-container {
  height: 0;
  width: 0;
}

h2 {
  font-size: calc(14px + 0.5vw);
}

h3 {
  font-size: calc(11px + 0.5vw);
}

.drink-list-elements{
  list-style-type: none;
  padding: 0;
  font-size: calc(14px + 0.5vw);
}

.api-credit{
  color: #666;
  font-size: calc(10px + 0.35vw);
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

.see-sidebar-info {
  background-color: #555;
  border-radius: 10px;
  margin-bottom: 5px;
}

.location-list-options {
  margin: 10px 0 10px 0;
  padding: 10px 0 10px 0;
  border: 1px solid grey;
  border-radius: 10px;

}

input {
  width: 65%;
}


